button, input, option, select {
    outline: none;
}
:root {
    --shadow: 0px 0px 20px rgba(0, 0, 0, 0.096);
}
.custom-selection option {
    background-color: #fff;
    border: unset;
}
.close-btn {
    display: none;
    position: absolute;
    top: 25px;
    right: 25px;
    background-color: transparent;
    outline: none;
    border: none;
}
.modal-window {
    width: 100%;
    background-color: #fff;
    margin-top: 100px;
    border-radius: 5px;
}
.default-input {
    background: #FFFFFF;
    box-shadow: var(--shadow);
    border-radius: 20px;
    padding: 15px 20px;
    color: #8A8A8A;
    border: none;
    font-size: 14px;
}
.photo-and-user-data-box {
    padding: 30px 20px;
    display: flex;
    justify-content: space-between;
}
.photo-box {
    width: 32%;
    max-width: 200px;
}
.photo-wrapper {
    width: 100%;
    height: auto;
    border-radius: 50%;
    overflow: hidden;
}
.photo-container {
    position: relative;
}
.plus-btn {
    position: absolute;
    bottom: 17%;
    right: 17%;
    width: 20%;
    height: 20%;
    background: #333333;
    border-radius: 50%;
    border: none;
}
#reg-form {
    padding-bottom: 100px;
}
.plus-btn svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 50%;
    height: 50%;
}
.profile-photo {
    width: 100%;
}
.modal-social-media-icons {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.social-media-icon {
    margin: 0 auto;
    width: 30%;
    height: auto;
    margin-top: 30px;
}
.fb-icon {
    width: calc(30% - 4px);
}
.inst-icon {
    width: calc(30% - 4px);
}
.user-data-field {
    width: 58%;
} 
.top-input {
    width: 100%;
    margin-top: 15px;
}
.gender-form {
    margin-top: 20px;
}
.gender-form input {
    display: none;
}
.gender-form {
    width: 100%;
    max-width: 190px;
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    color: #B4B4B4;
}
.gender-label::before {
    display: inline-block;
    margin-right: 5px;
    content: '';
    width: 20px;
    height: 20px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}
.gender-label:nth-child(1)::before {
    background-image: url(./img/gender-male.svg);
}
.gender-label:nth-child(2)::before {
    background-image: url(./img/gender-female.svg);
}
.gender-label>input:checked + span {
    color: #F5576C;
}
.gender-text {
    margin-top: 20px;
    font-size: 12px;
    /* or 15px */

    letter-spacing: 0.02em;

    color: #B4B4B4;
}
.data-input-box {
    width: 100%;
    margin-top: 10px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
}
.date-input {
    display: block;
    width: 28%;
    text-align: center;
}
.year-input {
    display: block;
    width: 40%;
    margin-right: 0;
    /* min-width: 160px; */
    text-align: center;
}
.password-fields {
    margin-bottom: 10px;
}
.bottom-input {
    width: 100%;
}
.language-select {
    border: none;
    width: 80%;
    margin: 0 auto;
    display: block;
    color: #B4B4B4;
    font-size: 16px;
}
.language-change-box {
    display: none;
    padding: 8px 0;
    font-size: 16px;
    color: #B4B4B4;
    width: 60%;
    margin: 0 auto;
    background: #FFFFFF;
    box-shadow: var(--shadow);
    border-radius: 20px;
}
.bottom-input {
        margin-top: 10px;
}
#mobile-language {
    display: block;
}
.custom-selection {
    font-size: 14px;
}
@media screen and (max-width: 400px) {
    .data-input-box input {
        padding: 20px 0;
    }
}
.custom-selection {
    border: none;
    padding: 2px 0;
    color: #B4B4B4;
    width: 100%;
    /* -webkit-appearance: none;  */
        /* background: url(assets/selectArrow.png) no-repeat right #BADA55; */
    
}
.selection-box {
    width: calc(100% - 40px);
    margin: 0 auto;
    margin-top: 15px;
    margin-bottom: 10px;
}
.specification-info-text {
    font-size: 12px;
    display: block;
    text-align: right;
    letter-spacing: 0.02em;
    width: 90%;
    margin-top: 10px;
    color: #B4B4B4;
    text-align: center;
    margin: 0 auto;
}
.terms-aceept-container {
    display: flex;
    width: 90%;
    margin: 0 auto;
    margin-top: 33px;
}
.checkbox-label {
    position: relative;
    width: 18px;
    height: 18px;
    display: inline-block;
    border: 2px solid #000000;
}
.check-symbol {
    top: -50%;
    right: -60%;
    width: 20px;
    height: 20px;
    position: absolute;
    display: none;
    background-image: url(./img/check.svg);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}
.checkbox-label > input:checked + .check-symbol {
    display: block;
    
}
.checkbox-label input {
    display: none;
}
.terms-of-use-text {
    font-size: 12px;
     letter-spacing: 0.02em;
     
     color: #000000;
    margin-left: 10px;
}
.terms-of-use-text a {
    color: #296dd3;
}
.modal-register-btn {
    max-width: 350px;
    width: 80%;
    display: block;
    margin: 0 auto;
    margin-top: 45px;
    opacity: 0.8;
    border: 2px solid #393939;
    box-sizing: border-box;
    backdrop-filter: blur(40px);
    background: none;
    /* Note: backdrop-filter has minimal browser support */

    font-size: 16px;
    line-height: 20px;
    align-items: center;

    color: #606060;
}
.select-options-box .bottom-input {
    width: calc(100% - 40px);
    margin: 0 auto;
    display: block;
    margin-top: 10px;
}
.select-options-box .bottom-input:first-child {
    margin-top: 0;
}

@media screen and (min-width: 1200px) {
    .modal-window {
        width: 90%;
        margin: 0 auto;
        margin-top: 80px;
        border-radius: 10px;
    }
    .default-input {
        padding: 20px;
    }
    .photo-and-user-data-box {
        padding: 30px 100px;
    }
    .photo-box {
        max-width: 100px;
    }
    .user-data-field {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .top-input {
        width: 49%;
    }
    .gender-form {
        width: 49%;
        align-self: center;
        max-width: 250px;
    }
    .date-input-label {
        width: 49%;
        align-self: center;
    }
    .gender-date-wrapper {
        width: 100%;
        display: flex;
        justify-content: space-between;
    }
    .gender-label {
        font-size: 16px;
    }
    .photo-and-user-data-box {
        position: relative;
    }
    .user-data-field {
        margin: 0 auto;
    }
    .photo-box {
        position: absolute;
    }
    .bottom-input {
        width: 49%;
    }
    .select-options-box {
        width: calc(57% - 100px);
        margin: 0 auto;
    }
    .password-fields {
        width: 100%;
        display: flex;
        justify-content: space-between;
        flex-wrap: nowrap;
    }
    .selection-box {
        width: 100%;
    }
    .custom-selection {
        font-size: 16px;
    }
    .language-change-box {
        padding: 8px 0;
    }
    .half-size-selection-wrapper {
        width: 100%;
        display: flex;
        justify-content: space-between;
    }
    .half-size-selection-wrapper .selection-box {
        width: 48%;
        margin: unset;
        padding: 15px 20px !important;
    }
    #mobile-language {
        display: none;
    }
    #pc-language {
        display: block;
        position: absolute;
        width: 85px;
        top: 50px;
        right: 110px;
    }
    .close-btn {
        width: 16px;
        height: 25px;
        display: block;
        background-image: url(./img/close.svg);
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
    }
    .modal-register-btn {
        width: 300px;
        margin-left: auto;
        margin-right: 110px;
    }
    .select-options-box .bottom-input {
        width: 49%;
    }
    /* .select-options-box .bottom-input:last-of-type {
        margin-bottom: 15px;
    } */
    .password-fields .bottom-input {
        margin: unset;
    }
    .half-size-selection-wrapper {
        margin-top: 15px;
    }
    .half-size-selection-wrapper .bottom-input {
        margin: unset;
    }
}